import React, { useEffect, useRef, useState } from "react";
import TabList from "./modules/TabList";
import Background from "./modules/Background";
import "./styles.css";
import { Image, ImageBackground } from "react-native";
import AboutMe from "./modules/AboutMe";
import backArrow from "../images/backArrow.png";
import gh from "../images/gh.png";
import lin from "../images/lin.png";
import yt from "../images/yt.png";
import useSound from "use-sound";
import cancel from "../sounds/cancel.mp3";
import hover from "../sounds/hover.mp3";

const layerStyle = {
  position: "absolute",
  marginTop: "10vh",
  height: "90vh",
  width: "100%",
  display: "flex",
};

export default function MenuScreen() {
  const [bg, setBg] = useState();
  const [bgComp, setBgComp] = useState();
  const [currList, setCurrList] = useState(1);
  useEffect(() => {
    setBgComp(<Background bg={bg} currList={currList} />);
  }, [bg]);
  return (
    <div className="MenuScreen">
      <NavigationBar />
      <div className="Background" style={layerStyle}>
        {bgComp}
      </div>
      <div style={layerStyle}>
        <TabLayer setBg={setBg} setCurrList={setCurrList} currList={currList} />
      </div>
    </div>
  );
}

function TabLayer(props) {
  const setBg = props.setBg;
  const setCurrList = props.setCurrList;
  const currList = props.currList;
  const [theta1, setTheta1] = useState(Math.PI);
  const [listComp, setListComp] = useState();

  const [height, setHeight] = useState();
  const [width, setWidth] = useState();
  const ref = useRef(null);

  useEffect(() => {
    setHeight(ref.current.clientHeight);
    setWidth(ref.current.clientWidth);
  }, []);

  useEffect(() => {
    let theta;
    let data;
    switch (currList) {
      case 1:
        theta = theta1;
        data = tabList1;
        setListComp(
          <TabList setBg={setBg} setCurrList={setCurrList} data={data} />
        );
        break;
      case 2:
        data = tabList2;
        setListComp(<AboutMe />);
        break;
      case 3:
        data = tabList3;
        setListComp(
          <TabList setBg={setBg} setCurrList={setCurrList} data={data} />
        );
        break;
      case 4:
        data = tabList4;
        setListComp(
          <TabList setBg={setBg} setCurrList={setCurrList} data={data} />
        );
        break;
    }
  }, [currList]);

  const tabList1 = [
    { destination: 2, text: "About Me", offset: 0 },
    { destination: 3, text: "Demos", offset: -1 },
  ];

  const tabList2 = [
    { destination: 1, text: "2P Mode", offset: 0 },
    { destination: 2, text: "3P Mode", offset: -1 },
  ];

  const tabList3 = [{ destination: 4, text: "Coming Soon...", offset: 0 }];

  const tabList4 = [
    { destination: 4, text: "Apple Scroller", offset: 0 },
    { destination: 4, text: "PokeText", offset: -1 },
    { destination: 4, text: "2048", offset: -1.5 },
  ];

  return (
    <div ref={ref} className="Tab-Layer">
      <div className="Tab-Container">
        <BackButton
          currList={currList}
          setCurrList={setCurrList}
          setBg={setBg}
        />
      </div>
      {listComp}
    </div>
  );
}

function BackButton(props) {
  const [currList, setCurrList, setBg] = [
    props.currList,
    props.setCurrList,
    props.setBg,
  ];
  const [bgColor, setBgColor] = useState();
  const [shown, setShown] = useState();
  const [playCancel] = useSound(cancel, { volume: 0.25 });
  const [playHover] = useSound(hover, { volume: 0.25 });

  const [stack, setStack] = useState([]);
  const [active, setActive] = useState(false);

  useEffect(() => {
    currList == 1 ? setShown("none") : setShown();
    let temp = [...stack];
    temp.push(currList);
    setStack(temp);
    console.log(temp);
  }, [currList]);

  const handleClick = () => {
    let temp = [...stack];
    setBg(temp.pop());
    setCurrList(temp.pop());
    // Pop-Pop!
    setStack(temp);
    playCancel();
  };

  const handleHover = () => {
    setBgColor("yellow");
    playHover();
  };

  return (
    <div
      onMouseEnter={() => handleHover()}
      onMouseLeave={() => setBgColor()}
      onClick={() => handleClick()}
      style={{ display: shown }}
      className="Back-Button"
    >
      <ImageBackground
        style={{
          width: "100%",
          height: "100%",
          clipPath:
            "polygon(-2% 50%, 52% -2%, 52% 23%, 100% 23%, 100% 77%, 52% 77%, 52% 102%)",
          backgroundColor: bgColor,
        }}
        source={backArrow}
      ></ImageBackground>
      <p>BACK</p>
    </div>
  );
}

function NavigationBar() {
  return (
    <div className="Nav-Bar">
      <p>adamhirata.github.io</p>
      <div>
        <img onClick={() => open("https://github.com/adamhirata")} src={gh} />
        <img
          onClick={() =>
            open("https://www.linkedin.com/in/adam-hirata-656552182")
          }
          src={lin}
        />
        <img
          onClick={() => open("https://www.youtube.com/watch?v=siwdP7Sfwvc")}
          src={yt}
        />
      </div>
    </div>
  );
}
