import "./stylesModules.css";
import React, { useEffect, useRef, useState } from "react";
import testImg from "../../images/test1.png";
import bgBase from "../../images/bgBase.png";
import imageCont from "../../images/imageCont.png";
import colorCont from "../../images/colorCont.png";
import png2 from "../../images/2.png";

import {
  Animated,
  Easing,
  Image,
  ImageBackground,
  StyleSheet,
  Text,
  View,
} from "react-native";
import { bounce, ease, linear } from "react-native/Libraries/Animated/Easing";

export default function Background(props) {
  const [bg, currList] = [props.bg, props.currList];
  const [dataComp, setDataComp] = useState();
  const [titleComp, setTitleComp] = useState();
  const [isDisplayed, setIsDisplayed] = useState();
  const [colorDeg, setColorDeg] = useState(180);
  const colorAnim = useRef(new Animated.Value(0)).current;

  const titleAnim = useRef(new Animated.Value(0)).current;

  const titleGrow = () => {
    titleAnim.setValue(0);
    Animated.timing(titleAnim, {
      toValue: 200,
      duration: 150,
      easing: linear,
      useNativeDriver: false,
    }).start();
  };

  const colorCycle = () => {
    Animated.loop(
      Animated.timing(colorAnim, {
        toValue: 359,
        duration: 1000,
        easing: Easing.linear,
        useNativeDriver: false,
      }),
      { iterations: -1 }
    ).start();
  };
  useEffect(() => {
    colorCycle();
  }, []);
  useEffect(() => {
    const curr = data[bg];
    if (curr && bg > 0) {
      const title = "";
      const desc = "";
      const img = "";
      const color = "";
      setDataComp(
        <div
          style={{
            display: "flex",
            position: "relative",
            flex: 1,
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <NextScreen source={curr.img ? curr.img : null} />
          <div className="BG-Desc">
            <p
              style={{ fontSize: curr.fontSize ? curr.fontSize : "16px" }}
              className="BG-DescText"
            >
              {curr.description ? curr.description : null}
            </p>
          </div>
        </div>
      );
    } else {
      setDataComp();
    }
  }, [bg]);
  const data = [
    {},
    {},
    { description: "Get to know me!", fontSize: "20px", img: png2 },
    { description: "Coming soon...", fontSize: "20px" },
  ];
  // [!] data: {title: string, desc: string, img: require(string), color: string}

  useEffect(() => {
    console.log(titles[currList]);
    currList == 2 ? setIsDisplayed({ display: "none" }) : setIsDisplayed();
    setColorDeg(titles[currList] ? titles[currList].colorDeg : 180);
    setTitleComp(
      <div
        style={{
          display: "flex",
          height: "100%",
          width: "100%",
          justifyContent: "center",
          alignItems: "center",
          position: "absolute",
        }}
      >
        <Animated.View
          style={{
            position: "absolute",
            left: "50px",
            top: "-10px",
            width: titleAnim,
            height: "35px",
            overflow: "hidden",
          }}
        >
          <Text
            style={{
              fontFamily: "ssbm",
              fontSize: "20px",
              fontStyle: "italic",
              color: "grey",
            }}
          >
            {
              /* THE TITLE COMPONENT GOES HERE */ titles[currList]
                ? titles[currList].title
                : null
            }
          </Text>
        </Animated.View>
      </div>
    );
    titleGrow();
  }, [currList]);

  const titles = [
    { title: null, colorDeg: 180 },
    { title: "Main Menu", colorDeg: 250 },
    { title: "About Me", colorDeg: 180 },
    { title: "Demos", colorDeg: 320 },
    { title: "Demos", colorDeg: 320 },
  ];

  return (
    <div className="BG-Container">
      {bg}
      {/* <Image
        style={{
          position: "absolute",
          width: "626px",
          height: "480px",
          filter: "drop-shadow(0 0 0 darkblue)",
        }}
        source={testImg}
      ></Image> */}
      <ImageBackground
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          position: "absolute",
          width: "1024px",
          height: "100%",
          filter: "drop-shadow(0 10px 10px black)",
          overflow: "hidden",
        }}
        source={bgBase}
      >
        <View style={styles.mainContainer}>
          {dataComp}
          {titleComp}
          <Image
            style={[
              styles.mainContainer,
              {
                filter:
                  "sepia(100%) hue-rotate(" + colorDeg + "deg) saturate(800%)",
                opacity: 0.7,
              },
            ]}
            source={testImg}
          />
          <View
            style={[
              {
                position: "absolute",
                width: 233,
                height: 325,
                right: "5px",
                justifyContent: "center",
              },
              isDisplayed,
            ]}
          >
            <Text
              style={{
                color: "white",
                textAlign: "center",
                textShadowRadius: "15px",
                textShadowColor: "white",
                fontWeight: "bold",
                transform: [{ rotate: "270deg" }, { translateY: "-105px" }],
              }}
            >
              N E X T {` `} S C R E E N
            </Text>
            <View
              style={{
                position: "absolute",
                width: 156,
                height: 225,
                right: -15,
                top: 40,
              }}
            >
              <Image
                style={[
                  { position: "absolute", width: 156, height: 225 },
                  {
                    filter:
                      "sepia(100%) hue-rotate(" +
                      colorDeg +
                      "deg) saturate(800%)",
                    opacity: 0.25,
                  },
                ]}
                source={colorCont}
              />
            </View>
            <Image
              style={[
                {
                  position: "absolute",
                  width: 233,
                  height: 325,
                },
                {
                  opacity: 0.7,
                },
              ]}
              source={imageCont}
            />
          </View>
        </View>
      </ImageBackground>
    </div>
  );
}

function NextScreen(props) {
  const [source] = [props.source];
  const animOf = useRef(new Animated.Value(0)).current;
  const fadeIn = () => {
    Animated.timing(animOf, {
      toValue: 0.5,
      duration: 200,
      easing: bounce,
      useNativeDriver: false,
    }).start();
  };

  useEffect(() => {
    fadeIn();
  }, []);

  return (
    <Animated.View
      style={{ position: "absolute", right: "-295px", opacity: animOf }}
    >
      <img className="Next-Screen" src={source} />
    </Animated.View>
  );
}

const styles = StyleSheet.create({
  mainContainer: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    position: "absolute",
    width: "626px",
    height: "480px",
  },
});
