import "./stylesModules.css";
import React, { useEffect, useRef, useState } from "react";
import { View, Text, Image, Easing, Animated, StyleSheet } from "react-native";
import { ease, linear } from "react-native/Libraries/Animated/Easing";
import avatar from "../../images/avatar.png";
import programming from "../../images/programming.png";
import communication from "../../images/communication.png";
import resolve from "../../images/resolve.png";
import gaming from "../../images/gaming.png";
import fitness from "../../images/fitness.png";
import AboutMeButtons from "./AboutMeButtons";

export default function AboutMe(props) {
  const animHeight = useRef(new Animated.Value(0)).current;
  const animOpacity = useRef(new Animated.Value(0)).current;

  const calculateAge = () => {
    const today = new Date().getTime();
    const bday = new Date("May 1, 1999 00:04:00").getTime();
    return Math.floor((today - bday) / (1000 * 3600 * 24 * 365));
  };

  const screenData = [
    <PokeText
      width={"100%"}
      height={"100%"}
      speed={15}
      messages={[
        `Hello! I'm Adam Hirata. I'm a ${calculateAge()}-year-old, aspiring web developer born and raised in Los Angeles.  I especially love front-end development and using the React framework.  Please use this site to get to know me better.`,
      ]}
      callback={console.log()}
      trigger={false}
    />,
    <Stats />,
    <Interests />,
  ];

  const [choice, setChoice] = useState();
  const [dataComp, setDataComp] = useState();
  const [courtesyComp, setCourtesyComp] = useState();

  const growScreen = () => {
    Animated.timing(animHeight, {
      toValue: 290,
      duration: 100,
      easing: ease,
      useNativeDriver: false,
    }).start();
  };

  const fadeIn = () => {
    Animated.timing(animOpacity, {
      toValue: 0.65,
      duration: 100,
      easing: linear,
      useNativeDriver: false,
    }).start();
  };

  useEffect(() => {
    growScreen();
    fadeIn();
  }, []);

  useEffect(() => {
    setChoice(screenData[dataComp]);
  }, [dataComp]);

  const createCourtesy = () => {
    setCourtesyComp(
      <div className="Courtesy-Comp">
        <p>Avatar created with an AI</p>
        <p>courtesy of waifulabs.com</p>
      </div>
    );
  };

  return (
    <div className="About-Me">
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          width: "626px",
          height: "480px",
          position: "relative",
        }}
      >
        <div className="About-Container">
          <Animated.View
            style={{
              display: "flex",
              width: 550,
              height: animHeight,
              backgroundColor: "aliceblue",
              border: "5px solid cornflowerblue",
              borderTop: "8px solid cornflowerblue",
              borderBottom: "8px solid cornflowerblue",
              borderRadius: "12px",
              opacity: animOpacity,
              boxShadow: "0px 0px 16px blue",
              position: "absolute",
            }}
          ></Animated.View>
          <Animated.View
            style={{
              display: "flex",
              flexDirection: "row",
              width: 550,
              height: animHeight,
              alignItems: "center",
              justifyContent: "space-evenly",
              overflowY: "hidden",
              padding: "5px",
            }}
          >
            <div style={{ display: "flex", justifyContent: "center" }}>
              <img
                className="Avatar"
                onMouseEnter={() => createCourtesy()}
                onMouseLeave={() => setCourtesyComp()}
                src={avatar}
              />
              {courtesyComp}
            </div>
            <div
              className="No-Scroll"
              style={{
                width: "60%",
                height: "95%",
                overflowY: "scroll",
              }}
            >
              {choice ? (
                choice
              ) : (
                <PokeText
                  width={"100%"}
                  height={"100%"}
                  speed={15}
                  messages={[
                    `Hello! I'm Adam Hirata. I'm a ${calculateAge()}-year-old, aspiring web developer born and raised in Los Angeles.  I especially love front-end development and using the React framework.  Please use this site to get to know me better.`,
                  ]}
                  callback={console.log()}
                  trigger={false}
                />
              )}
            </div>
          </Animated.View>
        </div>
        <AboutMeButtons setDataComp={setDataComp} />
      </div>
    </div>
  );
}

function PokeText(props) {
  let wordIndex = 0;
  let messageIndex = 0;
  let internalMsg = "";
  let finishedWord = true;
  let finished = false;
  const [speed, setSpeed] = useState(props.speed ? props.speed : 150);
  const [message, setMessage] = useState("");
  const [abort, setAbort] = useState();
  const trigger = props.trigger;
  const callback = () => {
    props.callback;
  };
  let current;

  function sleep(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  const nextWord = () => {
    if (finishedWord) {
      if (wordIndex >= props.messages.length) {
        finished = true;
        return;
      }
      messageIndex = 0;
      internalMsg = "";
      current = props.messages[wordIndex];
      finishedWord = false;
      nextChar();
      wordIndex++;
    } else if (!finishedWord) {
      setSpeed(20);
    }
  };

  const nextChar = async () => {
    if (abort) {
      abort.abort();
      setAbort();
      return;
    }
    await sleep(speed);
    if (messageIndex >= current.length) {
      finishedWord = true;
      setSpeed(props.speed ? props.speed : 150);
      return;
    }
    internalMsg += current.charAt(messageIndex);
    messageIndex++;
    setMessage(internalMsg);
    nextChar();
  };

  useEffect(() => {
    if (finished == true) {
      callback();
    }
    nextWord();
  }, [trigger]);

  useEffect(() => {
    return function cleanup() {
      setAbort(new AbortController());
    };
  }, []);

  return (
    <View style={[{ width: props.width, height: props.height }]}>
      <Text style={{ fontFamily: "ssbm", fontSize: 20 }}>{message}</Text>
    </View>
  );
}

function Stats() {
  return (
    <div className="Stats">
      <Skill
        props={{
          name: "Programming",
          imguri: programming,
          percentage: 80,
        }}
      />
      <Skill
        props={{
          name: "Communication",
          imguri: communication,
          percentage: 83,
        }}
      />
      <Skill
        props={{
          name: "Resolve",
          imguri: resolve,
          percentage: 99,
        }}
      />
      <Skill
        props={{
          name: "Gaming",
          imguri: gaming,
          percentage: 60,
        }}
      />
      <Skill
        props={{
          name: "Fitness",
          imguri: fitness,
          percentage: 10,
        }}
      />
    </div>
  );
}

function Skill(props) {
  const percentAnim = useRef(new Animated.Value(0)).current;
  const percentString = (val) => {
    return val + "%";
  };

  const percentGrow = () => {
    Animated.timing(percentAnim, {
      toValue: props.props.percentage,
      duration: 1000,
    }).start();
  };

  useEffect(() => {
    percentGrow();
  }, []);

  return (
    <View style={styles.skill}>
      <Image style={styles.skillImage} source={props.props.imguri} />
      <View style={styles.imageDivider}></View>
      <View>
        <View style={styles.percentBar}>
          <Animated.View
            style={{
              backgroundColor: "black",
              height: "100%",
              width: percentAnim,
            }}
          ></Animated.View>
        </View>
        <Text style={styles.skillText}>{props.props.name}</Text>
      </View>
    </View>
  );
}

function Interests() {
  const [curr, setCurr] = useState(0);
  const [dataComp, setDataComp] = useState();

  const data = [
    <div className="No-Scroll">
      <h1>Cooking</h1>
      <p>
        During the majority of my time in college until now, I have worked
        part-time at my favorite ramen shop, Santouka Ramen. Over the years, I
        have grown to enjoy cooking and kitchen work. Although I like many
        different foods, my favorite is Japanese cuisine.
      </p>
      <div style={{ height: "10px" }} />
      <p>
        Learning how to cook new things and expanding on my personal recipe book
        is my favorite way to spend time. I love cooking for friends and
        enjoying home-cooked meals together.
      </p>
    </div>,
    <div className="No-Scroll">
      <h1>Heritage</h1>
      <p>
        I am a fourth generation Japanese-American, with my Mom's side of the
        family first emigrating from Japan to Argentina before coming to
        America. As a kid I was quite confused about my heritage, however I have
        learned to embrace it as part of my identity.
      </p>
      <div style={{ height: "10px" }} />
      <p>
        During college, I had become heavily involved with a student
        organization called Nikkei Student Union. Through this club, I was able
        to learn more about Japanese and Japanese-American culture and share it
        with fellow students.
      </p>
      <div style={{ height: "10px" }} />
      <p>
        During my upperclassmen years, I took on leadership roles in the club. I
        became president for my school's club and president for the board which
        oversees events between chapters of our organization at many other SoCal
        schools. I believe it has been a great positive influence on my
        character and personal network.
      </p>
    </div>,
    <div className="No-Scroll">
      <h1>Games</h1>
      <p>
        In my free time, you'll likely find me playing games. My favorites are
        online chess, Animal Crossing, and Super Smash Brothers Melee. In fact,
        this whole website is made to look like the Smash Melee main menu.
      </p>
    </div>,
  ];

  const handleLeft = () => {
    curr == 0 ? setCurr(2) : setCurr(curr - 1);
  };

  const handleRight = () => {
    curr == 2 ? setCurr(0) : setCurr(curr + 1);
  };

  useEffect(() => {
    setDataComp(<p>loading...</p>);
    setDataComp(data[curr]);
  }, [curr]);
  return (
    <div className="Interests">
      {dataComp}
      <p id="handLeft" onClick={() => handleLeft()}>
        ☚
      </p>
      <p id="handRight" onClick={() => handleRight()}>
        ☛
      </p>
      <p id="index">{curr + 1}/3</p>
    </div>
  );
}

const styles = StyleSheet.create({
  skill: {
    flexDirection: "row",
    alignItems: "center",
    marginLeft: 25,
    marginBottom: 7,
  },
  skillImage: {
    width: 40,
    height: 40,
    resizeMode: "cover",
  },
  imageDivider: {
    height: 40,
    borderColor: "black",
    borderWidth: 1,
    marginLeft: 10,
    marginRight: 10,
  },
  skillText: {
    fontFamily: "ssbm",
    color: "black",
    fontSize: 14,
    marginTop: "0px",
  },
  percentBar: {
    padding: 2,
    width: 110,
    height: 14,
    borderColor: "black",
    borderWidth: 3,
    marginTop: 10,
  },
});
