import React, { useEffect, useRef, useState } from "react";
import useSound from "use-sound";
import "./stylesModules.css";
import hover from "../../sounds/hover.mp3";
import enter from "../../sounds/enter.mp3";
import cancel from "../../sounds/cancel.mp3";
import { Animated, Easing, View } from "react-native";

{
  /* <div
        style={{
          transform:
            "translate(" +
            ((width / 2) * Math.cos(theta1) + width / 2) +
            "px, " +
            (height / 2) * -Math.sin(theta1) +
            "px)",
        }}
      ></div> */
}

export default function TabList(props) {
  const ref = useRef(null);
  const animX = useRef(new Animated.Value(0)).current;
  const animY = useRef(new Animated.Value(0)).current;

  const moveX = () => {
    Animated.timing(animX, {
      toValue: 0,
      duration: 100,
      easing: Easing.linear,
      useNativeDriver: false,
    }).start();
  };
  const moveY = () => {
    Animated.timing(animY, {
      toValue: 0,
      duration: 100,
      easing: Easing.ease,
      useNativeDriver: false,
    }).start();
  };

  const data = [];
  const setBg = props.setBg;
  const setCurrList = props.setCurrList;

  useEffect(() => {
    animX.setValue(ref.current.clientWidth / 2 + 200);
    animY.setValue(-ref.current.clientHeight / 2);
    moveX();
    moveY();
  }, [data]);

  props.data.forEach((tab) =>
    data.push(
      <Tab
        setBg={setBg}
        setCurrList={setCurrList}
        destination={tab.destination}
        text={tab.text}
        offset={tab.offset}
      />
    )
  );
  return (
    <Animated.View
      style={{
        width: "100%",
        height: "100%",
        transform: [{ translateX: animX }, { translateY: animY }],
      }}
    >
      <div ref={ref} className="Tab-List">
        {data}
      </div>
    </Animated.View>
  );
}

function Tab(props) {
  const [destination, text, offset] = [
    props.destination,
    props.text,
    props.offset,
  ];
  const setBg = props.setBg;
  const setCurrList = props.setCurrList;

  const [backColor, setBackColor] = useState();
  const [fontColor, setFontColor] = useState("yellow");
  const [playHover] = useSound(hover, { volume: 0.5 }); //.5
  const [playEnter] = useSound(enter, { volume: 0.25 }); //.25

  const handleHover = () => {
    setBackColor("yellow");
    setFontColor("#444444");
    setBg(destination);
    playHover();
  };

  const handleLeave = () => {
    setBackColor();
    setFontColor("yellow");
  };

  const handleClick = () => {
    playEnter();
    setBg(0);
    setCurrList(destination);
  };

  return (
    <div
      style={{
        filter: "drop-shadow(0px 0px 5px yellow)",
        overflow: "hidden",
        margin: "15px",
        marginLeft: offset * 60 + "px",
      }}
    >
      <div className="Tab-Angle"></div>
      <div
        className="Tab"
        style={{ backgroundColor: backColor, color: fontColor }}
        onClick={() => handleClick()}
        onMouseEnter={() => handleHover()}
        onMouseLeave={() => handleLeave()}
      >
        {text}
      </div>
    </div>
  );
}
