import "./stylesModules.css";
import useSound from "use-sound";
import hover from "../../sounds/hover.mp3";
import enter from "../../sounds/enter.mp3";
import React, { useEffect, useRef, useState } from "react";
import { Animated, View } from "react-native";
import { ease } from "react-native/Libraries/Animated/Easing";

export default function AboutMeButtons(props) {
  const [curr, setCurr] = useState();
  const [butt0, setButt0] = useState(true);
  const [butt1, setButt1] = useState(false);
  const [butt2, setButt2] = useState(false);
  const [setDataComp] = [props.setDataComp];

  useEffect(() => {
    switch (curr) {
      case 0:
        setDataComp(0);
        setButt0(true);
        setButt1(false);
        setButt2(false);
        break;
      case 1:
        setDataComp(1);
        setButt0(false);
        setButt1(true);
        setButt2(false);
        break;
      case 2:
        setDataComp(2);
        setButt0(false);
        setButt1(false);
        setButt2(true);
        break;
    }
  }, [curr]);

  return (
    <div className="About-Buttons">
      <Button value={0} setCurr={setCurr} isSelected={butt0} />
      <Button value={1} setCurr={setCurr} isSelected={butt1} />
      <Button value={2} setCurr={setCurr} isSelected={butt2} />
    </div>
  );
}

function Button(props) {
  // props
  const [value, setCurr, isSelected] = [
    props.value,
    props.setCurr,
    props.isSelected,
  ];

  // animations
  const animHeight = useRef(new Animated.Value(0)).current;
  const growButton = () => {
    Animated.timing(animHeight, {
      toValue: 56,
      duration: 100,
      easing: ease,
      useNativeDriver: false,
    }).start();
  };

  // cosmetic states
  const [label, setLabel] = useState();
  const [bgColor, setBgColor] = useState();
  const [textColor, setTextColor] = useState("yellow");
  const [shadow, setShadow] = useState("drop-shadow(0 0 5px yellow)");
  const [borderColor, setBorderColor] = useState("yellow");

  // sounds
  const [playHover] = useSound(hover, { volume: 0.5 });
  const [playEnter] = useSound(enter, { volume: 0.25 });

  useEffect(() => {
    switch (value) {
      case 0:
        setLabel("About");
        break;
      case 1:
        setLabel("Stats");
        break;
      case 2:
        setLabel("Interests");
        break;
    }
    growButton();
  }, []);
  useEffect(() => {
    if (isSelected) {
      setBorderColor("grey");
      setTextColor("grey");
      setBgColor();
      setShadow();
    } else {
      setBorderColor("yellow");
      setTextColor("yellow");
      setShadow("drop-shadow(0 0 5px yellow)");
    }
  }, [isSelected]);
  const handleHover = () => {
    if (!isSelected) {
      playHover();
      setBgColor("yellow");
      setTextColor("black");
    }
  };
  const handleLeave = () => {
    if (!isSelected) {
      setBgColor();
      setTextColor("yellow");
    }
  };
  const handleClick = () => {
    if (!isSelected) {
      playEnter();
      setCurr(value);
    }
  };
  return (
    <Animated.View
      style={{
        height: animHeight,
        overflow: "hidden",
        filter: shadow,
      }}
    >
      <div
        style={{
          backgroundColor: bgColor,
          border: "3px solid",
          borderColor: borderColor,
          borderTopWidth: "5px",
          borderBottomWidth: "5px",
        }}
        onMouseEnter={() => handleHover()}
        onMouseLeave={() => handleLeave()}
        onClick={() => handleClick()}
        className="About-Button"
      >
        <p style={{ color: textColor }}>{label}</p>
      </div>
    </Animated.View>
  );
}
